import * as React from 'react';
import VideoListItem from './video_list_item';

const VideosList = (propsObj) => {

    //destruct propsObj param
    //const { dataVideos, onVideoSelect } = propsObj;

    //return HTML
    return (
        <ul className="col-md-4 list-group">

            {dataVideos.map(function (videoDataItem, i) {

                return <VideoListItem
                    key={videoDataItem.etag}
                    dataId={i}
                    dataVideo={propsObj.dataVideos}
                    onVideoSelect={propsObj.onVideoSelect}
                />
            })}

        </ul>
    );
}

export default VideosList


